  background-color: #36393f;
  width: 0px;
}
.chatScroll::-webkit-scrollbar-thumb {
  background-color: #212121;
  /* border: 4px solid transparent;
  border-radius: 4px;
  background-clip: padding-box; */
}
::-webkit-scrollbar {
  background-color: #36393f;
  height: 100%;
}
::-webkit-scrollbar-thumb {
  background-color: #212121;
  border: 4px solid transparent;
  border-radius: 20px;
  background-clip: padding-box;
}
.scroll-to-bottom {
  height: 100%;
  width: 100%;
  overflow: auto;
}
.image {
  min-width: 1000px;
  height: 90vh;
}
