.background-f {
  background: linear-gradient(#1a0136, #ca690e);
}
.wrapper {
  overflow-y: hidden;
  overflow-x: hidden;
  /* perspective: 10px; */
  /* display: flex; */
}
.header {
  justify-content: center;
  align-items: center;
  height: 100%;
  transform-style: preserve-3d;
  z-index: -1;
}
.background {
  transform: translateZ(-10px) scale(2);
}
.foreground {
  transform: translateZ(-5px);
}
.background,
.foreground {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

/* COLORS */
/* .crimson {
  color: #dc143c;
} */
.nav-item {
  color: white;
}
.nav-item:hover {
  color: #0bdbd0;
}
/* END COLORS */

/* NAV BAR */
.hamburger .bar {
  padding: 0;
  width: 34px;
  height: 3px;
  background-color: crimson;
  display: block;
  border-radius: 0px;
  transition: all 0.4s ease-in-out;
  position: absolute;
}
.bar1 {
  top: 15px;
  right: 6px;
  border-radius: 10px;
}
.bar2 {
  top: 25px;
  right: 6px;
  width: 32px;
  border-radius: 10px;
}
.bar3 {
  top: 35px;
  right: 6px;
  width: 32px;
  color: pink;
  border-radius: 10px;
}
@media (max-width: 1000px) {
  .show-hamburger {
    display: block;
  }
  .show-navbar {
    display: none;
  }
}
/* END NAV BAR */

/* DISCORD SERVER */
.message_container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}
.discord-bot-tag {
  border-radius: 4px;
}

.chat-message:hover {
  background-color: #2c2f36;
}
.chat-message:hover .msg-time {
  opacity: 1;
}
.scroll-to-bottom-btn {
  background: orange;
}
.chat-body {
  width: calc(100% - 420px);
}
.chat-footer {
  width: 100%;
}
.msg-time {
  opacity: 0;
  font-size: 9px;
}
.msg-time:hover {
  opacity: 1;
}
.online-icon {
  position: absolute;
  background-color: #00ab14;
  width: 18px;
  height: 18px;
  border: 3px solid #2f3136;
  border-radius: 100%;
}
/* END DISCORD SERVER */

/* MEDIA QUERIES */
/* .intro-section {
  min-height: calc(100vh - 40px);
} */
.calc-demo-height {
  min-height: calc(100% - 100px);
}
@media (max-width: 800px) {
  .shrink-sidebar {
    min-width: 80px;
  }
}
@media (max-height: 480px) {
  .intro-section-510px {
    min-height: 324px;
  }
  .intro-section {
    height: 90%;
  }
}
@media (max-height: 750px) {
  .break-intro {
    flex-direction: row;
    justify-content: center;
    margin-top: 100px;
    margin-left: 40px;
  }
  .mv-items-start-750 {
    align-items: start;
  }
  .fix-getdraftbot-btn {
    margin-top: 16px;
    margin-left: 10px;
  }
  .fix-intro-btns {
    flex-direction: row;
    margin-bottom: 20px;
  }
}
/* END MEDIA QUERIES */

